.position-list{
    z-index: 1;
    width: 100%;
}
.position-list li{
    overflow: hidden;
    padding: 0 14px;
    line-height: 50px;
}
.position-list li .position{
    float: left;
    color: #3c99d8;
    cursor: pointer;
    font-size: 14px;
    width: 400px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.time{
    font-style: normal;
    float: right;
    width: 70px;
    text-align: right;
    font-size: 14px;
    color: rgba(31,56,88,0.60);
}
.city{
    float: right;
    font-style:normal;
    font-size: 14px;
    color: rgba(0,0,0,0.60);
    margin-right: 70px;
    width: 202px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

}
